.modal {
  z-index: 100;
  position: fixed;
  top: 22vh;
  left: 10%;
  width: 80%;
  background-color: white;
  /* border: 1pt solid #f7e0ad; */
}

.modal__header {
  padding: 1rem 0.5rem;
  background: #454545;
  color: #f7e0ad;
}

.modal__header h2 {
  margin: 0.5rem;
  font-size: larger;
  font-weight: 500;
}

.modal__content {
  padding: 1rem 0.5rem;
  margin: 0.5rem;
}

.modal__footer {
  padding: 1rem 0.5rem;
  background-color: #454545;
}

.modal p {
  font-size: larger;
}

@media only screen and (min-width: 990px) {
  .modal {
    width: 60%;
    left: 20%;
  }
}